import React, { useEffect, useRef, useState, useCallback } from 'react';
import API from '../api';
import MediaUpload from './MediaUpload';

// Basic chat component: global + group rooms
export default function Chat() {
  const token = localStorage.getItem('token');
  const myId = (()=>{ try { const t = token?.split('.')[1]; if(!t) return null; const p = JSON.parse(atob(t.replace(/-/g,'+').replace(/_/g,'/'))); return p.id||p.userId||p._id||null; } catch { return null; } })();
  const [rooms, setRooms] = useState([]); // {id,name,members[]}
  const [activeRoom, setActiveRoom] = useState('global');
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [status, setStatus] = useState('');
  const [pendingImageUrl, setPendingImageUrl] = useState('');
  const socketRef = useRef(null);
  const bottomRef = useRef(null);
  const typingRef = useRef({});
  const [typingUsers, setTypingUsers] = useState([]);

  const fetchRooms = useCallback(async () => {
    try {
      const { data } = await API.get('/chat/rooms');
      setRooms(data);
    } catch (e) {
      setStatus('Erreur chargement salles');
    }
  }, []);

  const fetchMessages = useCallback(async (roomId) => {
    try {
      const { data } = await API.get(`/chat/rooms/${roomId}/messages`);
      setMessages(data);
    } catch (e) {
      setMessages([]);
    }
  }, []);

  useEffect(() => {
    if (!token) return;
    fetchRooms();
    let s;
    let cancelled = false;
    (async () => {
      try {
        const base = API?.defaults?.baseURL?.replace(/\/$/, '') || '';
        const wsUrl = base.replace(/^http/, 'ws') + '/chat';
        const { io } = await import('socket.io-client');
        if (cancelled) return;
        s = io(wsUrl, { auth: { token } });
        socketRef.current = s;

        s.on('connected', () => setStatus('Connect√©'));
        s.on('message', (msg) => {
          setMessages((prev) => prev.concat(msg));
        });
        s.on('error_msg', (m) => setStatus(m));
        s.on('messageDeleted', ({ id }) => {
          setMessages((prev) => prev.filter((m) => m.id !== id));
        });
        s.on('joined', ({ roomId }) => {
          if (roomId === activeRoom) fetchMessages(roomId);
        });
        s.on('typing', ({ user, typing }) => {
          typingRef.current[user] = typing ? Date.now() : 0;
          const list = Object.entries(typingRef.current)
            .filter(([, ts]) => ts && Date.now() - ts < 4000)
            .map(([u]) => u);
          setTypingUsers(list);
        });
      } catch (err) {
        setStatus('Connexion temps r√©el indisponible');
      }
    })();
    return () => { cancelled = true; if (s) s.disconnect(); };
  }, [token, fetchRooms, fetchMessages, activeRoom]);

  useEffect(() => { bottomRef.current?.scrollIntoView({ behavior: 'smooth' }); }, [messages]);

  useEffect(() => { if (activeRoom) fetchMessages(activeRoom); }, [activeRoom, fetchMessages]);

  const send = (e) => {
    e.preventDefault();
    if (!input.trim() && !pendingImageUrl) return;
    socketRef.current?.emit('message', { roomId: activeRoom, content: input, imageUrl: pendingImageUrl });
    setInput('');
    setPendingImageUrl('');
  };

  const onInput = (e) => {
    setInput(e.target.value);
    socketRef.current?.emit('typing', { roomId: activeRoom, typing: true });
  };

  const createRoom = async () => {
    const name = prompt('Nom du groupe ?');
    if (!name) return;
    try {
      const { data } = await API.post('/chat/rooms', { name });
      setRooms((r) => r.concat(data));
      setActiveRoom(data.id);
    } catch {
      setStatus('Erreur cr√©ation salle');
    }
  };

  return (
    <div style={{ display: 'flex', height: '85vh', gap: 16 }}>
      <aside style={{ width: 280, borderRight: '1px solid var(--line)', display: 'flex', flexDirection: 'column' }}>
        <div style={{ padding: 8, fontWeight: 'bold' }}>Salles</div>
        <div style={{ flex: 1, overflowY: 'auto' }}>
          {rooms.map((r) => (
            <div
              key={r.id}
              onClick={() => setActiveRoom(r.id)}
              style={{ cursor: 'pointer', padding: '6px 10px', background: activeRoom === r.id ? 'var(--accent-soft)' : 'transparent' }}
            >{r.name}</div>
          ))}
        </div>
        <button onClick={createRoom} style={{ margin: 8 }}>+ Groupe</button>
      </aside>
      <main style={{ flex: 1, display: 'flex', flexDirection: 'column' }}>
        <div style={{ padding: '4px 8px', borderBottom: '1px solid var(--line)', display: 'flex', justifyContent: 'space-between' }}>
          <strong>{rooms.find(r=>r.id===activeRoom)?.name || 'G√©n√©ral'}</strong>
          <span style={{ fontSize: 12, color: 'var(--muted)' }}>{status}</span>
        </div>
  <div style={{ flex: 1, overflowY: 'auto', padding: 12, background: 'var(--panel)' }}>
          {messages.map(m => {
            const dt = new Date(m.createdAt);
            const hh = String(dt.getHours()).padStart(2,'0');
            const mm = String(dt.getMinutes()).padStart(2,'0');
            return (
              <div key={m.id} style={{ marginBottom: 6, display:'flex', gap:8 }}>
                <span style={{ color: 'var(--muted)', fontSize: 11, marginRight: 4 }}>[{hh}:{mm}]</span>
                <span style={{ fontWeight: 'bold' }}>{m.username}: </span>
                <div style={{ flex:1 }}>
                  <span style={{ whiteSpace: 'pre-wrap' }}>{m.content}</span>
                  {m.imageUrl && (
                    <div>
                      <img loading="lazy" src={m.imageUrl.startsWith('/') ? (API.defaults.baseURL.replace(/\/$/,'') + m.imageUrl) : m.imageUrl} alt="img" style={{ maxWidth: 360, borderRadius: 8, marginTop: 6 }} />
                    </div>
                  )}
                </div>
                {(m.user === myId) && (
                  <button
                    className="week-btn"
                    title="Supprimer"
                    onClick={async ()=>{
                      try {
                        await API.delete(`/chat/messages/${m.id}`);
                        setMessages((prev)=> prev.filter(x=> x.id!==m.id));
                      } catch { setStatus('Suppression impossible'); }
                    }}
                  >üóëÔ∏è</button>
                )}
              </div>
            );
          })}
          <div ref={bottomRef} />
        </div>
        {typingUsers.length > 0 && (
          <div style={{ padding: '2px 10px', fontSize: 12, color: 'var(--muted)' }}>
            {typingUsers.join(', ')} √©crit...
          </div>
        )}
        <form onSubmit={send} style={{ display: 'flex', gap: 12, padding: 12, alignItems:'flex-start' }}>
          <textarea
            value={input}
            onChange={onInput}
            onKeyDown={(e) => { if (e.key === 'Enter' && !e.shiftKey) { send(e); } }}
            placeholder="Message (Entr√©e pour envoyer, Shift+Entr√©e pour nouvelle ligne)"
            style={{ flex: 1, resize: 'none', minHeight: 80 }}
          />
          <div style={{ display:'flex', flexDirection:'column', gap:8, width: 360, flexShrink: 0 }}>
            <MediaUpload onUploaded={(d)=>{ if(d.status==='approved' && d.url){ const full = d.url.startsWith('/') ? (API.defaults.baseURL.replace(/\/$/,'') + d.url) : d.url; setPendingImageUrl(full); } }} />
            {pendingImageUrl && (
              <div style={{ display:'flex', alignItems:'center', gap:6 }}>
                <img src={pendingImageUrl} alt="preview" style={{ width: 64, height: 64, objectFit:'cover', borderRadius: 6, flexShrink: 0 }} />
                <button type="button" onClick={()=> setPendingImageUrl('')}>Retirer</button>
              </div>
            )}
            <button type="submit">Envoyer</button>
          </div>
        </form>
      </main>
    </div>
  );
}
