import React, { useRef, useState } from 'react';
import API from '../api';

export default function MediaUpload({ onUploaded, showPreview = true, previewSize = 64 }) {
  const [file, setFile] = useState(null);
  const [status, setStatus] = useState('');
  const [preview, setPreview] = useState('');
  const inputRef = useRef(null);

  const submit = async (e) => {
    e?.preventDefault?.();
    if (!file) return;
    setStatus('Envoi...');
    const fd = new FormData();
    fd.append('file', file);
    try {
  const { data } = await API.post('/media/image', fd, { headers: { 'Content-Type': 'multipart/form-data' } });
  setStatus(data.status === 'approved' ? 'OK' : 'En attente mod√©ration');
  // Clear the initial local file and its preview
  setFile(null);
  if (inputRef.current) inputRef.current.value = '';
  // After upload: show a small server-side preview only if approved (has public url)
  const base = API?.defaults?.baseURL?.replace(/\/$/, '') || '';
  const absoluteUrl = data?.url ? (data.url.startsWith('/') ? (base + data.url) : data.url) : '';
  setPreview(data.status === 'approved' && absoluteUrl ? absoluteUrl : '');
  if (onUploaded) onUploaded({ ...data, absoluteUrl });
    } catch (e2) {
      const msg = e2.response?.data?.error || 'Erreur upload';
      setStatus(msg);
    }
  };

  return (
    <div style={{ display: 'flex', flexDirection: 'column', gap: 8 }}>
      <input
        type="file"
        accept="image/png,image/jpeg,image/webp"
        ref={inputRef}
        onChange={(e) => {
          const f = e.target.files?.[0];
          if (!f) return;
          if (f.size > 2 * 1024 * 1024) { // client hint; server enforces too
            setStatus('> 2MB refus√©'); return;
          }
          setFile(f);
          setPreview(URL.createObjectURL(f));
          setStatus('');
        }}
      />
      {showPreview && preview && (
        <img
          src={preview}
          alt="pr√©visualisation"
          style={{ width: previewSize, height: previewSize, objectFit: 'cover', border: '1px solid var(--line)', borderRadius: 6 }}
        />
      )}
      <button className="week-btn" type="button" onClick={submit} disabled={!file}>Uploader l‚Äôimage</button>
      {status && <small style={{ color: 'var(--muted)' }}>{status}</small>}
    </div>
  );
}
